import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import API_BASE_URL from "../config"; // go up one level

function InterviewPrep() {
  const navigate = useNavigate();

  //  Auth protection
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      alert("Please login first!");
      navigate("/");
    }
  }, [navigate]);

  //  State
  const [topic, setTopic] = useState("");
  const [difficulty, setDifficulty] = useState("easy");
  const [questions, setQuestions] = useState([]);
  const [loading, setLoading] = useState(false);

  //  Fetch AI-generated questions
  const handleGenerate = async () => {
    if (!topic.trim()) return alert("Please enter a topic!");
    setLoading(true);
    setQuestions([]);

    try {
      const response = await fetch(`${API_BASE_URL}/ai/generate`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ topic, difficulty }),
      });

      const data = await response.json();
      console.log(" API Response:", data);

      setQuestions(Array.isArray(data) ? data : [data]);
    } catch (error) {
      console.error(" Error generating question:", error);
      alert("Failed to generate questions. Check your backend connection.");
    } finally {
      setLoading(false);
    }
  };

  //  Logout handler
  const handleLogout = () => {
    localStorage.removeItem("token");
    localStorage.removeItem("user");
    navigate("/");
  };

  return (
    <div style={{ padding: "2rem", fontFamily: "Arial, sans-serif" }}>
      <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
        <h1> AI Interview Question Generator</h1>
        <button
          onClick={handleLogout}
          style={{
            background: "tomato",
            color: "white",
            border: "none",
            padding: "0.5rem 1rem",
            borderRadius: "5px",
            cursor: "pointer",
          }}
        >
          Logout
        </button>
      </div>

      <div style={{ marginTop: "1.5rem" }}>
        <input
          type="text"
          placeholder="Enter a topic (e.g., JavaScript)"
          value={topic}
          onChange={(e) => setTopic(e.target.value)}
          style={{ marginRight: "1rem", padding: "0.5rem" }}
        />

        <select
          value={difficulty}
          onChange={(e) => setDifficulty(e.target.value)}
          style={{ marginRight: "1rem", padding: "0.5rem" }}
        >
          <option value="easy">Easy</option>
          <option value="medium">Medium</option>
          <option value="hard">Hard</option>
        </select>

        <button onClick={handleGenerate} disabled={loading}>
          {loading ? "Generating..." : "Generate"}
        </button>
      </div>

      <div style={{ marginTop: "2rem" }}>
        {questions.length > 0 ? (
          questions.map((q, index) => (
            <div
              key={index}
              style={{
                marginBottom: "1.5rem",
                padding: "1rem",
                border: "1px solid #ddd",
                borderRadius: "8px",
                background: "#f9f9f9",
              }}
            >
              <h3 style={{ color: "#333" }}>
                {index + 1}. {q.questionText}
              </h3>
              <p style={{ color: "#555" }}>{q.answer}</p>
            </div>
          ))
        ) : (
          !loading && <p>No questions generated yet.</p>
        )}
      </div>
    </div>
  );
}

export default InterviewPrep;
