import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Editor from "@monaco-editor/react";
import confetti from "canvas-confetti";
import {
  Play,
  Send,
  CheckCircle2,
  XCircle,
  ChevronDown,
  ChevronUp,
  Zap,
  Clock,
  TrendingUp,
} from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "../components/ui/card";
import { Button } from "../components/ui/button";
import { Badge } from "../components/ui/badge";
import { Progress } from "../components/ui/progress";
import Layout from "../components/Layout";
import WelcomeHeader from "../components/WelcomeHeader";

const CodingPractice = () => {
  const [code, setCode] = useState(`function twoSum(nums, target) {
  // Your code here
}`);
  const [testResults, setTestResults] = useState(null);
  const [showFeedback, setShowFeedback] = useState(false);
  const [expandedTest, setExpandedTest] = useState(null);
  const [progress, setProgress] = useState(65);
  const [streak, setStreak] = useState(7);

  const currentProblem = {
    title: "Two Sum",
    difficulty: "Easy",
    difficultyColor: "success",
    topics: ["Arrays", "Hash Table"],
    description:
      "Given an array of integers nums and an integer target, return indices of the two numbers such that they add up to target.",
    testCases: [
      {
        input: "nums = [2,7,11,15], target = 9",
        expected: "[0,1]",
        output: null,
        passed: null,
      },
      {
        input: "nums = [3,2,4], target = 6",
        expected: "[1,2]",
        output: null,
        passed: null,
      },
      {
        input: "nums = [3,3], target = 6",
        expected: "[0,1]",
        output: null,
        passed: null,
      },
    ],
  };

  // Simulated AI feedback
  const aiFeedback = {
    efficiency: "Good",
    timeComplexity: "O(n)",
    spaceComplexity: "O(n)",
    suggestions: [
      "Consider using a hash map for O(1) lookups.",
      "Your current approach is optimal for this problem.",
    ],
  };

  const handleRunCode = () => {
    const results = [
      {
        input: "nums = [2,7,11,15], target = 9",
        expected: "[0,1]",
        output: "[0,1]",
        passed: true,
      },
      {
        input: "nums = [3,2,4], target = 6",
        expected: "[1,2]",
        output: "[1,2]",
        passed: true,
      },
      {
        input: "nums = [3,3], target = 6",
        expected: "[0,1]",
        output: "[0,1]",
        passed: true,
      },
    ];
    setTestResults(results);
    setShowFeedback(true);
  };

  const handleSubmit = () => {
    handleRunCode();
    const allPassed =
      testResults?.every((t) => t.passed) ||
      currentProblem.testCases.every(() => true);
    if (allPassed) {
      confetti({
        particleCount: 100,
        spread: 70,
        origin: { y: 0.6 },
      });
      setProgress((p) => Math.min(p + 10, 100));
      setStreak((s) => s + 1);
    }
  };

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      alert("Please login first!");
      window.location.href = "/";
    }
  }, []);

  return (
    <Layout>
      <div className="space-y-6">
        <WelcomeHeader />

        {/* Progress Section */}
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center space-x-4 flex-1">
            <div className="flex-1">
              <div className="flex items-center justify-between mb-1">
                <span className="text-sm font-medium text-gray-700">
                  Weekly Progress
                </span>
                <span className="text-sm text-gray-500">{progress}%</span>
              </div>
              <Progress value={progress} />
            </div>
            <div className="flex items-center space-x-2 bg-orange-50 px-4 py-2 rounded-lg">
              <Zap className="h-4 w-4 text-orange-500" />
              <span className="font-semibold text-orange-700">
                {streak} day streak 
              </span>
            </div>
          </div>
        </div>

        {/* Problem Card */}
        <Card className="mb-6">
          <CardHeader>
            <div className="flex items-start justify-between">
              <div>
                <CardTitle className="text-2xl mb-2">
                  {currentProblem.title}
                </CardTitle>
                <div className="flex items-center space-x-2 mb-2">
                  <Badge variant={currentProblem.difficultyColor}>
                    {currentProblem.difficulty}
                  </Badge>
                  {currentProblem.topics.map((topic) => (
                    <Badge key={topic} variant="secondary">
                      {topic}
                    </Badge>
                  ))}
                </div>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            <p className="text-gray-700 mb-4">{currentProblem.description}</p>
          </CardContent>
        </Card>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Code Editor */}
          <Card>
            <CardHeader>
              <CardTitle>Code Editor</CardTitle>
            </CardHeader>
            <CardContent>
              <div
                className="border rounded-lg overflow-hidden mb-4"
                style={{ height: "400px" }}
              >
                <Editor
                  height="400px"
                  defaultLanguage="javascript"
                  value={code}
                  onChange={(value) => setCode(value || "")}
                  theme="vs-light"
                  options={{
                    minimap: { enabled: false },
                    fontSize: 14,
                    wordWrap: "on",
                  }}
                />
              </div>
              <div className="flex space-x-3">
                <Button onClick={handleRunCode} className="flex-1">
                  <Play className="mr-2 h-4 w-4" />
                  Run Code
                </Button>
                <Button
                  onClick={handleSubmit}
                  variant="default"
                  className="flex-1"
                >
                  <Send className="mr-2 h-4 w-4" />
                  Submit
                </Button>
              </div>
            </CardContent>
          </Card>

          {/* Test Cases & AI Feedback */}
          <div className="space-y-6">
            {/* Test Cases */}
            <Card>
              <CardHeader>
                <CardTitle>Test Cases</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {(testResults || currentProblem.testCases).map(
                    (test, index) => (
                      <motion.div
                        key={index}
                        initial={{ opacity: 0, y: 10 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="border rounded-lg p-3"
                      >
                        <div className="flex items-center justify-between mb-2">
                          <div className="flex items-center space-x-2">
                            {test.passed === true ? (
                              <CheckCircle2 className="h-5 w-5 text-green-500" />
                            ) : test.passed === false ? (
                              <XCircle className="h-5 w-5 text-red-500" />
                            ) : (
                              <div className="h-5 w-5 rounded-full border-2 border-gray-300" />
                            )}
                            <span className="font-medium text-sm">
                              Test Case {index + 1}
                            </span>
                          </div>
                          <button
                            onClick={() =>
                              setExpandedTest(
                                expandedTest === index ? null : index
                              )
                            }
                            className="text-gray-500 hover:text-gray-700"
                          >
                            {expandedTest === index ? (
                              <ChevronUp className="h-4 w-4" />
                            ) : (
                              <ChevronDown className="h-4 w-4" />
                            )}
                          </button>
                        </div>
                        {expandedTest === index && (
                          <motion.div
                            initial={{ opacity: 0, height: 0 }}
                            animate={{ opacity: 1, height: "auto" }}
                            className="text-xs text-gray-600 space-y-1 mt-2"
                          >
                            <p>
                              <strong>Input:</strong> {test.input}
                            </p>
                            <p>
                              <strong>Expected:</strong> {test.expected}
                            </p>
                            {test.output && (
                              <p>
                                <strong>Output:</strong> {test.output}
                              </p>
                            )}
                          </motion.div>
                        )}
                      </motion.div>
                    )
                  )}
                </div>
              </CardContent>
            </Card>

            {/* AI Feedback */}
            <AnimatePresence>
              {showFeedback && (
                <motion.div
                  initial={{ opacity: 0, scale: 0.95 }}
                  animate={{ opacity: 1, scale: 1 }}
                  exit={{ opacity: 0, scale: 0.95 }}
                >
                  <Card className="bg-gradient-to-br from-indigo-50 to-purple-50 border-indigo-200">
                    <CardHeader>
                      <CardTitle className="flex items-center space-x-2">
                        <Zap className="h-5 w-5 text-primary" />
                        <span>AI Feedback</span>
                      </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                      <div>
                        <div className="flex items-center justify-between mb-2">
                          <span className="text-sm font-medium">
                            Code Efficiency
                          </span>
                          <Badge variant="success">
                            {aiFeedback.efficiency}
                          </Badge>
                        </div>
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div className="bg-white rounded-lg p-3">
                          <div className="flex items-center space-x-2 mb-1">
                            <Clock className="h-4 w-4 text-primary" />
                            <span className="text-xs font-medium text-gray-600">
                              Time Complexity
                            </span>
                          </div>
                          <p className="text-sm font-semibold">
                            {aiFeedback.timeComplexity}
                          </p>
                        </div>
                        <div className="bg-white rounded-lg p-3">
                          <div className="flex items-center space-x-2 mb-1">
                            <TrendingUp className="h-4 w-4 text-primary" />
                            <span className="text-xs font-medium text-gray-600">
                              Space Complexity
                            </span>
                          </div>
                          <p className="text-sm font-semibold">
                            {aiFeedback.spaceComplexity}
                          </p>
                        </div>
                      </div>

                      <div>
                        <p className="text-sm font-medium mb-2">
                          Suggested Improvements:
                        </p>
                        <ul className="space-y-1">
                          {aiFeedback.suggestions.map((suggestion, idx) => (
                            <li
                              key={idx}
                              className="text-sm text-gray-700 flex items-start"
                            >
                              <span className="mr-2"></span>
                              {suggestion}
                            </li>
                          ))}
                        </ul>
                      </div>
                    </CardContent>
                  </Card>
                </motion.div>
              )}
            </AnimatePresence>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CodingPractice;
